body {
  font-family: sans-serif;
  background-color: #00092c;
}

.brand {
  color: white;
  text-align: center;
  font-family: "Open Sans";
  font-weight: bolder;
  font-size: 35px;
  margin-left: 5px;
}

.navmenu {
  background-color: #051f52;
}
.home-top {
  background-color: #00092c;
  padding: 100px 0px 100px 0px;
}

.container {
  position: relative;
  text-align: center;
  color: white;
}

.intro1 {
  font-size: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-88%, -140%);
}

.intro2 {
  font-size: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-88%, 0%);
}
.we-do {
  background-color: #00092c;
}

/*
flip card
*/
.card {
  border: 0px;
}
.card-body {
  background-color: #00092c;
  padding: 5rem 1rem;
}
.card-flip > div {
  backface-visibility: hidden;
  transition: transform 800ms;
  transition-timing-function: linear;

  width: 100%;
  height: 100%;
  margin: 0;

  display: flex;
}

.card-front {
  transform: rotateY(0deg);
}

.card-back {
  transform: rotateY(180deg);
  position: absolute;
  top: 0;
}

.card-flip:hover .card-front {
  transform: rotateY(-180deg);
}

.card-flip:hover .card-back {
  transform: rotateY(0deg);
}
.get-started {
  padding: 100px 0px 100px 0px;
}
.we-do {
  padding: 100px 0px 100px 0px;
  margin: 20px 0px;
}

.nav-link {
  font-size: 25px;
  font-weight: bolder;
}

.home-newsletter {
  background: #051f52;
  padding: 80px 0;
  margin: 20px 0px;
}

.home-newsletter .single {
  max-width: 650px;
  margin: 0 auto;
  text-align: center;
  position: relative;
  z-index: 2;
}

.home-newsletter .single h2 {
  font-size: 30px;
  color: white;
  text-transform: uppercase;
  margin-bottom: 40px;
}

.home-newsletter .single .form-control {
  height: 50px;
  background: rgba(255, 255, 255, 0.6);
  border-color: transparent;
  border-radius: 20px 0 0 20px;
}

.home-newsletter .single .form-control:focus {
  box-shadow: none;
  border-color: #243c4f;
}

.home-newsletter .single .btn {
  min-height: 50px;
  border-radius: 0 20px 20px 0;
  background: #243c4f;
  color: #fff;
}

/*==================== 
  Footer 
====================== */

/* Main Footer */
footer .main-footer {
  padding: 25px 0;
  background: #051f52;
}
footer ul {
  padding-left: 0;
  list-style: none;
}

/* Copy Right Footer */
.footer-copyright {
  background: #00092c;
  padding: 5px 0;
}
.footer-copyright .logo {
  display: inherit;
}

.footer-copyright ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.footer-copyright ul li {
  border-left: 1px solid #505050;
  display: inline-block;
  line-height: 12px;
  margin: 0;
  padding: 0 8px;
}
.footer-copyright ul li a {
  color: #d9c484;
}
.footer-copyright ul li:first-child {
  border: medium none;
  padding-left: 0;
}
.footer-copyright p {
  color: #d9c484;
  margin: 2px 0 0;
}

/* Footer Top */
.footer-top {
  background: #252525;
  padding-bottom: 30px;
  margin-bottom: 30px;
  border-bottom: 3px solid #222;
}

/* Footer transparent */
footer.transparent .footer-top,
footer.transparent .main-footer {
  background: transparent;
}
footer.transparent .footer-copyright {
  background: none repeat scroll 0 0 rgba(0, 0, 0, 0.3);
}

/* Footer light */
footer.light .footer-top {
  background: #f9f9f9;
}
footer.light .main-footer {
  background: #f9f9f9;
}
footer.light .footer-copyright {
  background: none repeat scroll 0 0 rgba(255, 255, 255, 0.3);
}

/*==================== 
  Widgets 
====================== */
.widget {
  padding: 20px;
  margin-bottom: 40px;
}

.widget.no-box {
  padding: 0;
  background-color: transparent;
  margin-bottom: 40px;
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  -ms-box-shadow: none;
  -o-box-shadow: none;
}
.widget.subscribe p {
  margin-bottom: 18px;
}
.widget li a {
  color: black;
}
.widget li a:hover {
  color: #4b92dc;
}
.widget-title {
  margin-bottom: 20px;
}
.widget-title span {
  background: #839fad none repeat scroll 0 0;
  display: block;
  height: 1px;
  margin-top: 25px;
  position: relative;
  width: 20%;
}
.widget-title span::after {
  background: inherit;
  content: "";
  height: inherit;
  position: absolute;
  top: -4px;
  width: 50%;
}
.widget-title.text-center span,
.widget-title.text-center span::after {
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
}
.widget .badge {
  float: right;
  background: #7f7f7f;
}

.custom-light h5,
.custom-light p,
.custom-light div,
.custom-light span,
.custom-light small {
  color: white;
}

ul.social-footer2 {
  margin: 0;
  padding: 0;
  width: auto;
}
ul.social-footer2 li {
  display: inline-block;
  padding: 0;
}
ul.social-footer2 li a:hover {
  background-color: #d2d6d9;
}
ul.social-footer2 li a {
  display: block;
  height: 30px;
  width: 30px;
  text-align: center;
}
